.menu {
  z-index: 10;
  @include sm-block {
    position: absolute;
    top: 90%;
    width: 100%;
    left: 0;
    overflow: hidden;
  }
  &__list {
    @extend %regular-r;
    display: flex;
    font-size: 14px;
    text-transform: uppercase;
    @include sm-block {
      padding-bottom: 30px;
      flex-direction: column;
      background-image: linear-gradient(#1a1a40, #252678);
      transform: translateY(-101%);
      font-size: 18px;
    }
  }
  &__link {
    position: relative;
    padding: 10px 15px;
    display: block;
    color: $color_white;
    transition: all .3s;
  }
  &__item {
    a {
      position: relative;
      padding: 10px 15px;
      display: block;
      color: $color_white;
      transition: all .3s;
      &:hover {
        color: $color_white;
        &:after {
          content: "";
          position: absolute;
          bottom: 0;
          left: 50%;
          margin-left: -32%;
          width: 64%;
          height: 2px;
          background: $color_white;
          @include sm-block {
            margin-left: auto;
            left: 0;
            width: 100%;
          }
        }
      }
    }
  }

  &_show {
    @include sm-block {
      transform: translateY(0);
    }
  }
}

