.menu-bnt {
  display: none;
  position: absolute;
  right: 5px;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
  width: 30px;
  height: 30px;
  padding-right: 15px;
  background: 0;
  border: 0;
  user-select: none;
  outline: 0;

  @include sm-block {
    display: flex;
    cursor: pointer;
  }
  &__icon {
    position: relative;
    display: inline-block;
    width: 30px;
    height: 3px;
    vertical-align: bottom;
    background: $color_white;
    transition: background .3s .5s;

    &:before {
      top: 10px;
    }

    &:after {
      top: -10px;
    }

    &:before, &:after {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      height: 3px;
      background: $color_white;
      transform-origin: 50% 50%;
      transition: top .3s .3s, transform .3s;
    }

    &_show {
      background-color: transparent;

      &:before {
        transform: rotate3d(0, 0, 1, 45deg);
      }

      &:after {
        transform: rotate3d(0, 0, 1, -45deg);
      }

      &:before, &:after {
        transition: top .3s, transform .3s .5s;
        top: 0;
      }
    }
  }
}

