            border-right: 1px solid $color_maven_blue;
          }
          &:hover {
            background: $color_maven_blue;
            a {
              color: $color_white;
            }
          }
          a {
            color: $color_maven_blue;
          }
        }
      }
      img {
        vertical-align: middle;
        z-index: 1;
        transition: all .5s;
      }
    }

    &__about {
      margin-top: 60px;
      font-size: 18px;
      .name {
        @extend %regular-m;
        margin-bottom: 30px;
        font-size: 26px;
        line-height: 34px;
        color: $color_white;
      }
      .position {
        @extend %light-r;
        line-height: 27px;
        color: $color_white;
        margin-bottom: 40px;
      }
    }
  }
}


.clients {
  background-image: url('../img/bg-clients.jpg');
