      @include xs-block {
        margin-bottom: 60px;
        flex-direction: column;
      }

      &__photo {
        flex: 1 0 30%;
        text-align: center;
      }
      &__opinion {
        flex: 1 1 55%;
        font-size: 18px;
        line-height: 27px;
        @include xs-block {
          text-align: left;
        }

        .name {
          @extend %regular-m;
          display: block;
          text-transform: uppercase;
          color: $color_turquoise;
        }
        .pos {
          @extend %light-r;
          line-height: 36px;
          color: $color_gray;
