.story {
  @include col();
  @include size(4);
  @include size-s(12);
  display: flex;
  flex-direction: column;
  @include s-block {
    width: 80%;
    margin: 0 auto 30px;
    text-align: center;
  }

  .story-banner {
    position: relative;
    &__date {
      @extend %bold-m;
      padding: 15px;
      position: absolute;
      bottom: 6%;
      left: -3%;
      background: $color_turquoise;
      font-size: 30px;
      color: #fff;

      span {
        @extend %light-r;
        display: block;
        font-size: 15px;
      }
    }
  }

  h5 {
    padding: 20px 0;
  }

  &__info {
    @extend %regular-r;
    margin-bottom: 20px;
    font-size: 15px;
    line-height: 25px;
    color: $color_white;
  }
