}

.h3about {
  margin: 80px 0;
  text-align: center;
  font-size: 15px;
  color: #ffffff;
}

h4 {
  font-size: 14px;
  text-transform: uppercase;
  color: #ffffff;
}

h5 {
  text-transform: uppercase;
  font-size: 14px;
  color: #ffffff;
}

.header {
  position: relative;
  background-image: url("../img/bg-clients.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  text-align: left;
  z-index: 1;
  transition: background .5s;
}

.header__1 {
  background-image: url("../img/mb-img0.jpeg");
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  text-align: left;
  z-index: 1;
  transition: background .5s;
}

.header .container {
  position: relative;
  z-index: 6;
}

.header__items {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 30px 0;
  z-index: 10;
}

.header__items_bg {
  background-color: transparent;
}

.header__logo {
  padding: 0 30px;
}

.header__logo a {
  font-size: 30px;
  color: #ffffff;
}

.header__btn {
  padding: 10px 30px;
  box-sizing: border-box;
  background: transparent;
  border: 2px solid #ffffff;
  font-size: 18px;
  color: #ffffff;
  transition: background .3s;
}

.header__btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

.menu {
  z-index: 10;
}

.menu__list {
  display: flex;
  font-size: 14px;
  text-transform: uppercase;
}

.menu__link {
  position: relative;
  padding: 10px 15px;
  display: block;
  color: #ffffff;
  transition: all .3s;
}

.menu__item a {
  position: relative;
  padding: 10px 15px;
  display: block;
  color: #ffffff;
  transition: all .3s;
}

.menu__item a:hover {
  color: #ffffff;
}

.menu__item a:hover:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -32%;
  width: 64%;
  height: 2px;
  background: #ffffff;
}

.banner-slider {
  display: flex;
  flex-wrap: wrap;
  margin-left: -2%;
  margin-right: -2%;
  margin-top: 9em;
  font-size: 16px;
}

.banner-slider__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 2%;
  margin-right: 2%;
  width: 21%;
  position: relative;
  padding: 1.6em 0;
  text-align: left;
  text-transform: uppercase;
  font-size: 1.3em;
  color: #ffffff;
}

.banner-slider__item .slide-nr {
  font-size: 1.5em;
}

.banner-slider__item .running-line {
  position: absolute;
  top: -2px;
  left: 0;
  height: 2px;
  width: 0;
  background: #0024fc;
}

.menu-bnt {
  display: none;
  position: absolute;
  right: 5px;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
  width: 30px;
  height: 30px;
  padding-right: 15px;
  background: 0;
  border: 0;
  user-select: none;
  outline: 0;
}

.menu-bnt__icon {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 3px;
  vertical-align: bottom;
  background: #ffffff;
  transition: background .3s .5s;
}

.menu-bnt__icon:before {
  top: 10px;
}

.menu-bnt__icon:after {
  top: -10px;
}

.menu-bnt__icon:before,
.menu-bnt__icon:after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background: #ffffff;
  transform-origin: 50% 50%;
  transition: top .3s .3s, transform .3s;
}

.menu-bnt__icon_show {
  background-color: transparent;
}

.menu-bnt__icon_show:before {
  transform: rotate3d(0, 0, 1, 45deg);
}

.menu-bnt__icon_show:after {
  transform: rotate3d(0, 0, 1, -45deg);
}

.menu-bnt__icon_show:before,
.menu-bnt__icon_show:after {
  transition: top .3s, transform .3s .5s;
  top: 0;
}

.about {
  background: #252678;
}

.about .team {
  display: flex;
  flex-wrap: wrap;
  margin-left: -2%;
  margin-right: -2%;
}

.about .team__mask {
  position: relative;
  transition: all .5s;
}

.about .team__mask:hover {
  transform: translate(-10px, -10px);
}

.about .team__mask .mask {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 2;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(rgba(37, 38, 120, 0.8), rgba(37, 38, 120, 0.8));
  color: #fff;
  flex-direction: column;
  font-size: 18px;
}

.about .team__mask .mask:hover {
  opacity: 1;
}

.about .team__mask .mask i {
  font-size: 1.33em;
  margin: 0.4em;
}

.about .team__mask .mask span {
  display: block;
  font-size: 1em;
  text-transform: uppercase;
}

.about .team__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 2%;
  margin-right: 2%;
  width: 29.33333%;
  cursor: pointer;
  transition: background .5s;
}

.about .team__item:hover {
  position: relative;
  background: #ffffff;
}

.about .team__item img {
  vertical-align: middle;
  z-index: 1;
  transition: all ease;
}

.direction {
  background: #252678;
}

.direction__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -2%;
  margin-right: -2%;
}

.direction__wrapper .block {
  word-wrap: break-word;
  padding-left: 2%;
  padding-right: 2%;
  width: 29.33333%;
  margin-top: 30px;
  padding-bottom: 30px;
  display: flex;
  color: #ffffff;
}

.direction__wrapper .block__icon {
  width: 30%;
  text-align: center;
}

.direction__wrapper .block__icon i {
  font-size: 40px;
  color: #ffffff;
}

.direction__wrapper .block__info {
  width: 70%;
}

.direction__wrapper .block__paragraph {
  padding-top: 10px;
  font-size: 14px;
  line-height: 22px;
  color: #ffffff;
}

.work {
